const form = document.querySelector(".form");
const btnsub = document.querySelector(".btnsub");

form.addEventListener("submit", function(e) {
  e.preventDefault(); //przerywamy domyślną wysyłkę

  let isValid = true; //pomocnicza zmienna
  const errors = []; //tablica błędów

  const formElements = [...this.elements];
  let [name, email, message, button] = formElements;

  // test first input
  if (!name.value.length) {
    isValid = false;
    errors.push("Wpisz imię i nazwisko");
    console.log(errors);
  }
  // test email input
  const mailReg = new RegExp("^[0-9a-z_.-]+@[0-9a-z.-]+.[a-z]{2,3}$", "i");
  if (!mailReg.test(email.value)) {
    isValid = false;
    errors.push("Podaj adres email");
  }
  // test textarea

  // czyszczenie inputów
  email.value = "";
  name.value = "";
  message.value = "";
  // wysyłka formularza
  if (isValid) {
    this.submit();
  } else {
    return false;
  }
});

const btn = document.querySelector(".page-nav-toggle");
const menu = document.querySelector(".page-nav-list");
const li = document.querySelectorAll(
  "#main-page > header > div > nav > ul > li"
);

function showMenu() {
  menu.classList.toggle("show");
}

menu.addEventListener("click", (e) => {
  let list = document.querySelectorAll(
    "#main-page > header > div > nav > ul > li > a"
  );

  for (let i = 0; i < list.length; i++) {
    if (e.target.dataset.id === list[i].dataset.id) {
      menu.classList.toggle("show");
    } else {
      list[i].className = "show";
    }
  }
});

btn.addEventListener("click", showMenu);
